.Header{
    width: 100vw;
    height: 10vh;
    display: grid;
    grid-template-columns: 100px 100px;
    align-items: center;
    justify-content: space-between; 
}

.Header__logo{
    height: 55px;
    width: 46px;
    position: relative;
    z-index: 3;
    margin-left: 17px;
    padding-top: 10px;
}

.Header__Navbar{
    position: absolute;
    z-index: 3;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(37, 3, 6, 0.95);
    -webkit-animation-name: clicked;
            animation-name: clicked;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    text-transform: uppercase;
    justify-content: center;
    align-items: center;
    display: none;
}

.Header__Menu{
    display: none;
}

.Header__Navbar ul{ 
   width: 50vw;
   display: grid;
   grid-template-columns: 1fr;
   grid-template-rows: 50px 50px 50px 50px;
   font-size: 18px;
}

li{ 
    margin: 0;
    padding: 0;
    border-top: 1px solid #f0f0f0;
    text-align: center;
    display: grid;
    align-items: center;
    font-size: 17px;
  }

.Header__Navbar a:focus{
    color: #a43e59;
    margin-left: 20%;
    width: 60%;
    border-top: 1px solid #a43e59;
    border-bottom: 1px solid #a43e59;
}

li:last-child{
    border-bottom: 1px solid #f0f0f0;
}

.BurgerSpin{
    position: relative;
    z-index: 3;
}

.fadeInRight {
    display: grid;
    -webkit-animation-name: fadeInRight;
    animation-name: fadeInRight;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    }
    @-webkit-keyframes fadeInRight {
    0% {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
    }
    100% {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
    }
    }
    @keyframes fadeInRight {
    0% {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
    }
    100% {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
    }
    } 

.fadeOutLeft {
    -webkit-animation-name: fadeOutLeft;
    animation-name: fadeOutLeft;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    }
    @-webkit-keyframes fadeOutLeft {
    0% {
    opacity: 1;
    }
    100% {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
    }
    }
    @keyframes fadeOutLeft {
    0% {
    opacity: 1;
    }
    100% {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);

    }
    }


a{  
    text-decoration: none;
    color: #fff;
}


@media only screen and (min-device-width : 480px) and (max-device-width : 768px) {

}

@media only screen and (min-device-width : 768px) and (max-device-width : 1024px) {
    .Header__Menu{
        display: grid;
        width: 100vw;
        justify-content: center;
        text-transform: uppercase;
    }
    ul{ 
        width: 20vw;
        display: grid;
        grid-template-columns: repeat(8, 1fr);
        grid-gap: 10px;
        grid-auto-rows: 100px;
        grid-template-areas: 
          "a a a a b b b b"
          "a a a a b b b b"
          "c c c c d d d d"
          "c c c c d d d d";
        align-items: start;
      
    }
    li{
        border: none;
        display: grid;
        justify-content: center;
    }
    
    .Header__Menu a:hover{
        border-radius: 24px;
        background-color: #a83a5e;
        padding: 5px 7px;
    }

    li:last-child{
        border: none;
    }
    .Header__Menu{
        display: grid;
    }

    .BurgerSpin{
        display: none!important;
    }
    .Header__Navbar{
        display: none;
    }
    .Header__logo{
        display: none;
    }
}

@media only screen and (min-device-width : 1024px) and (max-device-width : 1440px) {
    .Header__Menu{
        display: grid;
        width: 100vw;
        justify-content: center;
        text-transform: uppercase;
    }
    ul{ 
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        grid-gap: 10px;
        grid-auto-rows: 50px;
        grid-template-areas: "a b c d e";
        align-items: center;
    }

    li:last-child{
        grid-area: c;
    }

    li{
        border: none;
        display: grid;
        justify-content: center;
    }

    .Header__Menu a:hover{
        border-radius: 24px;
        background-color: #a83a5e;
        padding: 5px 7px;
    }

    li:last-child{
        border: none;
    }
  
    .BurgerSpin{
        display: none!important;
    }
    .Header__logo{
        display: none;
    }
}
@media only screen and (min-device-width : 1440px) and (max-device-width : 2540px) {
    .Header__Menu{
        display: grid;
        width: 100vw;
